import { CST } from "../CST";
let score = 0;
let level = 0;
let restart = true;
export class EndScene extends Phaser.Scene {

    constructor() {
        super({ key: CST.SCENES.END });
    }
    init(data) {
        score = data.finalScore;
        level = data.gameLevel;
    }

    preload() {
        this.sound.stopAll(); // stops menu sound 
        this.sound.play(CST.AUDIO.END_SOUND);  
    }

    create() {
        this.add.image(0, 0, CST.IMAGE.END_BG).setOrigin(0).setDepth(0);// background
        let playButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height / 2.4, CST.IMAGE.PLAY).setDepth(1);
        let scoreText = this.add.text(285, 100, "Final Score:", { fontSize: "32px", fill: "#FFF" });
        scoreText.setText("Final Score:" + score);

        // make buttons interactive
        playButton.setInteractive();

        playButton.on("pointerup", () => {
            this.scene.sleep(CST.SCENES.END);
            this.scene.start(CST.SCENES.PLAY, {gameLevel: level, sceneRestart: restart});// restart play scene
        })
    }
}

