import { CST } from "../CST";
//********NPCs variables */
let diverSummon = false; // diver appears or not on screen
let diversToRescue = 0;
var getShark = 0 // shark childrens
var getEnemy = 0 // enemies childrens 
const diverRescued = new Phaser.Events.EventEmitter();// new event 
const makeSoundFx = new Phaser.Events.EventEmitter();// new event 
//****player variables ********
let playerDirection; // -1 is left and 1 is right
let submarineOnSurface;
let submarineSpeed;
let oxigenLevel;
var oxigenBar;
let pausePlayer;
let diversXPos;
//********other variables */
let score = 0;
var scoreText = 0;
let level = 0;
let sceneInit = true;
let advanceLevel = false;
var actionKey = 0;

export class PlayScene extends Phaser.Scene {

    constructor() {
        super({ key: CST.SCENES.PLAY });
    }

    initPlayerVariables() { // actually init all player variables.
        playerDirection = 1; // -1 is left and 1 is right
        submarineOnSurface = true;
        submarineSpeed = 120;
        oxigenLevel = 100;
        oxigenBar = 0;
        pausePlayer = false;
        diversXPos = 200;
    }

    init(data) {
        this.initPlayerVariables();

        if (data.sceneRestart == true) {// reset this scence but avoid to init events and anims again
            level = data.gameLevel;
            sceneInit = false;
        }
    }

    preload() {
        this.sound.stopAll(); // stops menu sound 
        this.load.plugin("rexvirtualjoystickplugin", "assets/plugins/virtualJoystick.js", true);
    }

    create() {

        // add background
        this.background = this.physics.add.image(0, 0, CST.IMAGE.GAME_BG).setOrigin(0).setDepth(0);// background
        this.physics.world.add(this.background);
        //this.physics.world.setBounds(0,0,800,600);

        if (this.sys.game.device.input.touch == true) {// true if touch screen is present
            //***********virtual joystick******** */
            this.background.setInteractive(); // use background as fire button

            this.joyStick = this.plugins.get("rexvirtualjoystickplugin").add(this, {// virtual joystick deafults
                x: 120,
                y: 520,
                radius: 100,
                base: this.add.circle(0, 0, 50, 0x888888),
                thumb: this.add.circle(0, 0, 25, 0xcccccc),
                dir: "4dir",
                forceMin: 16,
                fixed: true,
                enable: true
            });

            this.controls = this.joyStick.createCursorKeys();// state of cursor keys

            this.background.on("pointerdown", () => {
                actionKey = true;
            });

        } else {
            this.controls = this.input.keyboard.addKeys("up, down, left, right");// if no touch, use keyboard
            actionKey = this.input.keyboard.addKey("space");// use space as action keys
        }

        // ************submarine animatiion***************
        if (sceneInit && level == 0) { // create anims and events only once
            this.anims.create({
                key: "left",
                frameRate: 10,
                frames: this.anims.generateFrameNumbers(CST.SPRITE.SUBMARINE, {
                    start: 0,
                    end: 2
                })
            });

            this.anims.create({
                key: "right",
                frameRate: 10,
                frames: this.anims.generateFrameNumbers(CST.SPRITE.SUBMARINE, {
                    start: 3,
                    end: 5
                })
            });

            //********************* shark animation**************
            this.anims.create({
                key: "sharkLeftToRight",
                frameRate: 5,
                repeat: -1,
                frames: this.anims.generateFrameNumbers(CST.SPRITE.SHARK, {
                    frames: [0, 1, 2, 1, 0]
                })
            })

            //*****************diver animation *****************
            this.anims.create({
                key: "diverLeftToRight",
                frameRate: 6,
                repeat: -1,
                frames: this.anims.generateFrameNumbers(CST.SPRITE.DIVER, {
                    frames: [0, 1, 2, 1, 0, 2]
                })
            })

            // *****bomb animation or direction change*******
            this.anims.create({
                key: "bombRight",
                frameRate: 0,
                frames: this.anims.generateFrameNumbers(CST.SPRITE.BOMB, {
                    frames: [0]
                })
            })

            this.anims.create({
                key: "bombLeft",
                frameRate: 0,
                frames: this.anims.generateFrameNumbers(CST.SPRITE.BOMB, {
                    frames: [1]
                })
            })

            //********enemies animation****************
            this.anims.create({
                key: "enemiLeftToRight",
                frameRate: 6,
                repeat: -1,
                frames: this.anims.generateFrameNumbers(CST.SPRITE.SUBMARINE, {
                    frames: [6, 7, 8]
                })
            })

            /***********Events****** */
            makeSoundFx.on("sound", this.soundFx, this); // sound event handler
            diverRescued.on("rescued", this.rescueEvent, this); // sound event handler
        }

        //******oxigen bar ****** */
        oxigenBar = this.add.graphics({
            fillStyle: {
                color: 0x000FF // white
            }
        });

        oxigenBar.fillRect(380, 540, oxigenLevel, 18).setDepth(1);
        // ************add  text***********

        scoreText = this.add.text(16, 16, "score: 0", { fontSize: "32px", fill: "#000" });
        this.add.text(280, 540, "OXIGEN:", { fontSize: "24px", fill: "#000" });

        // ******** sound********
        this.sound.play(CST.AUDIO.GAME_MUSIC, { // load main theme
            loop: true,
            volume: 0.2
        });

        //add player submarine**************
        this.submarine = this.physics.add.sprite(400, 75, CST.SPRITE.SUBMARINE, 0).setScale(1);
        this.submarine.setSize(80, 30).setOffset(6, 2);// change submarine hitbox size
        this.submarine.body.setCollideWorldBounds(true); // set boundaries for submarine sprite

        //add enemy submarine****************
        this.manyEnemies = this.physics.add.group();
        this.manyEnemies.defaults.setImmovable = true; // group deafults

        //submarine group****************
        this.manySharks = this.physics.add.group();
        this.manySharks.defaults.setImmovable = true; // group deafults

        //divers group**************
        this.manyDivers = this.physics.add.group();
        this.manyDivers.defaults.setImmovable = true;

        this.diversSaved = this.add.group();

        // bomb group*****************
        this.rightBombs = this.physics.add.group();
        this.rightBombs.defaults.setImmovable = true; // group deafults

        this.leftBombs = this.physics.add.group();
        this.leftBombs.defaults.setImmovable = true; // group deafults

        // add a timed events
        this.timedEvents();

        // *************sprite colition******************

        this.physics.add.collider(this.background, this.rightBombs, function (_background, rightBombs) { // right bombs collition detect

            if (rightBombs.body.checkWorldBounds()) {// checks if the bombs are in the world bounds
                rightBombs.destroy();// if not destroy bombs
            }
        });

        this.physics.add.collider(this.background, this.leftBombs, function (_background, leftBombs) { // left bomb collition detect

            if (leftBombs.body.checkWorldBounds()) {// checks if the bombs are in the world bounds
                leftBombs.destroy();// if not destroy bombs
            }
        });

        this.physics.add.collider(this.background, this.manyDivers, function (_background, manyDivers) { // divers collition detect

            if (manyDivers.body.checkWorldBounds()) {// checks if the divers are in the world bounds
                manyDivers.destroy();// if not destroy diver
            }
        });

        this.physics.add.collider(this.submarine, this.manySharks, function (submarine, _shark) { // collition detect
            submarine.destroy();
        });

        this.physics.add.collider(this.manyDivers, this.submarine, function (_submarine, diver) { // collition detect
            diver.destroy();
            score += 20;
            scoreText.setText('Score: ' + score);
            makeSoundFx.emit("sound", 2);
            diverRescued.emit("rescued");
        });

        this.physics.add.collider(this.submarine, this.manyEnemies, function (submarine, _enemy) { // collition detect
            submarine.destroy();
        });

        this.physics.add.collider(this.manyDivers, this.rightBombs, function (manyDivers, rightBombs) { // collition detect
            manyDivers.destroy();
            rightBombs.destroy();
        });

        this.physics.add.collider(this.manyDivers, this.leftBombs, function (manyDivers, leftBombs) { // collition detect
            manyDivers.destroy();
            leftBombs.destroy();
        });

        this.physics.add.collider(this.manySharks, this.rightBombs, function (manySharks, rightBombs) { // collition detect
            manySharks.destroy();
            rightBombs.destroy();
            score += 5;
            scoreText.setText('Score: ' + score);
            makeSoundFx.emit("sound", 1);
        });

        this.physics.add.collider(this.manySharks, this.leftBombs, function (manySharks, leftBombs) { // collition detect
            manySharks.destroy();
            leftBombs.destroy();
            score += 5;
            scoreText.setText('Score: ' + score);
            makeSoundFx.emit("sound", 1);
        });

        this.physics.add.collider(this.manyEnemies, this.rightBombs, function (manyEnemies, rightBombs) { // collition detect
            manyEnemies.destroy();
            rightBombs.destroy();
            score += 10;
            scoreText.setText('Score: ' + score);
            makeSoundFx.emit("sound", 0);
        });

        this.physics.add.collider(this.manyEnemies, this.leftBombs, function (manyEnemies, leftBombs) { // collition detect
            manyEnemies.destroy();
            leftBombs.destroy();
            score += 10;
            scoreText.setText('Score: ' + score);
            makeSoundFx.emit("sound", 0);
        });
    }

    timedEvents() {
        this.time.removeAllEvents();
        this.time.addEvent({ delay: Phaser.Math.Between(2000, (4000 - (level * 100))), callback: this.sharkEvent, callbackScope: this, loop: true }); // create a shark 
        this.time.addEvent({ delay: Phaser.Math.Between(3000, (4000 - (level * 100))), callback: this.enemySubEvent, callbackScope: this, loop: true });// create a enemy sub
        this.time.addEvent({ delay: Phaser.Math.Between(2000, 6000), callback: this.diverEvent, callbackScope: this, loop: true });// create a diver
        this.time.addEvent({ delay: 1000, callback: this.oxigenTimer, callbackScope: this, loop: true });// create a diver
    }

    //*****Create sound effects********* */
    soundFx(effect) {// emits one time sounds no repeat
        switch (effect) {
            case 0:
                this.sound.play(CST.AUDIO.ENEMY_SOUND);// enemy killed
                break;
            case 1:
                this.sound.play(CST.AUDIO.SHARK_SOUND);// shark killed
                break;
            case 2:
                this.sound.play(CST.AUDIO.DIVER_SOUND);// diver rescued sound
                break;
            case 3:
                this.sound.play(CST.AUDIO.OXIGEN);//oxigen refill sound
                break;
        }
    }

    /******Dispay number of divers rescued on screen******** */
    rescueEvent() {
        if (!submarineOnSurface && this.diversSaved.getLength() < 6) {
            diversXPos += 50;
            diversToRescue = 1;
            this.diversSaved.add(this.add.sprite(diversXPos, 580, CST.SPRITE.DIVER, 0).setScale(0.15));

            if (this.diversSaved.getLength() == 6) {
                diversToRescue = 6;
                advanceLevel = true;
                this.sound.play(CST.AUDIO.RESCUE_COMPLETED);
            }
        } else if (submarineOnSurface && diversToRescue > 0) {
            diversXPos -= 50;
            diversToRescue--;
            const getDivers = this.diversSaved.getChildren();
            const i = (this.diversSaved.getLength() - 1);
            this.diversSaved.remove(getDivers[i], true, true);
        }
    }

    /********Oxigen bar timer******** */
    oxigenTimer() {

        if (submarineOnSurface && pausePlayer) {// if submarine is on surface oxigen is refilled 
            diverRescued.emit("rescued");

            if (oxigenLevel < 100) {
                oxigenLevel += 15; // recharge oxigen
            }

            if (oxigenLevel > 100) {
                oxigenLevel = 100;
            }

            if (diversToRescue == 0 && oxigenLevel == 100) {
                pausePlayer = false;// unpause player
                this.sound.stopByKey(CST.AUDIO.OXIGEN);// stop oxigen refill sound
                this.sound.removeByKey(CST.AUDIO.OXIGEN_LOW);// stop oxigen refill sound
                this.sound.resumeAll();// resume music

                if (advanceLevel) {
                    advanceLevel = false;
                    level++;
                    console.log(level);
                    this.timedEvents();
                }
            } else {
                makeSoundFx.emit("sound", 3);// oxigen refill
            }
        }

        if (!submarineOnSurface) {// if the submarine is underwater oxigen is consumed
            oxigenBar.clear();

            if (oxigenLevel -= 2, oxigenLevel < 1) { // if no oxigen left end game.
                this.endGame();
            }

            if (oxigenLevel == 30) {
                this.sound.play(CST.AUDIO.OXIGEN_LOW, { // load main theme
                    loop: true,
                    volume: 0.3
                });
            }
        }
        oxigenBar.fillRect(380, 540, oxigenLevel, 18).setDepth(1);
    }

    testIfBodyExist(x, y, sharks, enemies) { // check if the place if already in use by another body

        for (let i = 0; i < this.manySharks.getLength(); i++) {

            if (getShark[i].body.x > 800) {  // looping the array to destroy sharks that are out of bounds.
                this.manySharks.remove(getShark[i], true, true);
            }

            if (sharks[i].body.hitTest(x, y)) {
                return true;
            }
        }

        for (let i = 0; i < this.manyEnemies.getLength(); i++) {

            if (getEnemy[i].body.x > 800) {  // looping the array to destroy sharks that are out of bounds.
                this.manyEnemies.remove(getEnemy[i], true, true);
            }

            if (enemies[i].body.hitTest(x, y)) {
                return true;
            }

        }
        return false
    }

    //************Timed events**************** */
    sharkEvent() { // callback shark timed event
        const randomSharkPosition = Phaser.Math.Between(120, 500); // randomize shark position
        getShark = this.manySharks.getChildren(); // get all children of the group 

        if (this.testIfBodyExist(0, randomSharkPosition, getShark, getEnemy) == false) {
            this.manySharks.add(this.physics.add.sprite(0, randomSharkPosition, CST.SPRITE.SHARK, 0).setScale(0.15));
            this.manySharks.playAnimation("sharkLeftToRight"); // play shark left animation. 
            this.manySharks.setVelocityX(100);// set shark left to right velocity 

            if (diverSummon) {// divers appears on screen in front of shark
                diverSummon = false;
                this.manyDivers.add(this.physics.add.sprite(60, randomSharkPosition, CST.SPRITE.DIVER, 0).setScale(0.15));
                this.manyDivers.playAnimation("diverLeftToRight"); // play diver
                this.manyDivers.setVelocityX(100);
            }
        }
    }

    enemySubEvent() {
        const randomEnemyPosition = Phaser.Math.Between(120, 550); // randomize enemy sub position
        getEnemy = this.manyEnemies.getChildren(); // get all children of the group 

        if (this.testIfBodyExist(0, randomEnemyPosition, getShark, getEnemy) == false) {
            this.manyEnemies.add(this.physics.add.sprite(0, randomEnemyPosition, CST.SPRITE.SUBMARINE, 0).setScale(0.8))
            this.manyEnemies.playAnimation("enemiLeftToRight"); // play diver
            this.manyEnemies.setVelocityX(100);
        }

        for (let i = 0; i < this.manyEnemies.getLength() && this.manyEnemies.getLength() != 0; i++) { // make sure that the sprite y possition isnt used

            if (getEnemy[i].body.x > 800) {  // looping the array to destroy sharks that are out of bounds.
                this.manyEnemies.remove(getEnemy[i], true, true);
            }
        }
    }

    diverEvent() {
        diverSummon = true;
    }

    endGame() {

        //this.input.input.shutdown();
        this.sound.removeByKey(CST.AUDIO.GAME_MUSIC);// stop oxigen refill sound
        this.scene.stop(CST.SCENES.PLAY);// stop current scene
        this.scene.run(CST.SCENES.END, { finalScore: score, gameLevel: level });// go to score scene*/
    }

    update(time, delta) {// delta 16.6666 @60fps

        if (!submarineOnSurface && this.submarine.y < 75) {// check if submarine is on surface

            if (!submarineOnSurface && this.diversSaved.getLength() == 0) {// if the submarine reaches the surface without any diver end game
                this.endGame();
            }
            submarineOnSurface = true;
            pausePlayer = true;
            this.submarine.setVelocityY(0);
            this.submarine.setVelocityX(0);
            this.sound.pauseAll();// stop music
        } else if (this.submarine.y > 75) {// if submarine is diving
            submarineOnSurface = false;
        }
        //*********player movement******** */
        if (this.submarine.active == true) { // inputs only ative while player is alive

            if (pausePlayer) return;// if refilling oxigen the submarine isnt movable

            if (actionKey == true || this.input.keyboard.checkDown(actionKey, 500)) {
                this.sound.play(CST.AUDIO.PLAYER_FIRE_SOUND);

                if (actionKey == true) actionKey = false;

                if (playerDirection == 1) {
                    this.rightBombs.add(this.physics.add.sprite(this.submarine.x + (this.submarine.displayWidth / 2), this.submarine.y, CST.SPRITE.BOMB, 0).setScale(0.10));
                    this.rightBombs.setVelocityX(150);
                    this.rightBombs.playAnimation("bombRight");
                } else {
                    this.leftBombs.add(this.physics.add.sprite(this.submarine.x - (this.submarine.displayWidth / 2), this.submarine.y, CST.SPRITE.BOMB, 0).setScale(0.10));
                    this.leftBombs.setVelocityX(-150);
                    this.leftBombs.playAnimation("bombLeft");
                }
            }

            if (this.controls.right.isDown == true) { // left submarine  sprite movement
                this.submarine.setVelocityX(submarineSpeed);
                this.submarine.play("left", true);
                playerDirection = 1;
            }

            if (this.controls.left.isDown == true) { // right submarine sprite movement
                this.submarine.setVelocityX(-submarineSpeed);
                this.submarine.play("right", true);
                playerDirection = -1;
            }

            if ((this.controls.left.isUp && this.controls.right.isUp)) { // not moving on x axis
                this.submarine.setVelocityX(0);
            }

            if (this.controls.up.isDown == true && !submarineOnSurface) { // up submarine  sprite movement              
                this.submarine.setVelocityY(-submarineSpeed);
            }

            if (this.controls.up.isUp && this.controls.down.isUp) { // not moving on x axis
                this.submarine.setVelocityY(0);
            }

            if (this.controls.down.isDown == true) { // down submarine sprite movement
                this.submarine.setVelocityY(submarineSpeed);
            }

        } else {
            this.endGame();
        }
    }
}
